<!--管理中心-->
<template>
  <div id="manager">
    <el-form
      :model="ruleForm2"
      status-icon
      :rules="rules2"
      ref="ruleForm2"
      label-width="100px"
      class="demo-ruleForm"
    >
      <h3 class="alter">修改你的密码</h3>
      <el-form-item label="身份证号" prop="cardId" class="pass">
        <el-input
          type="text"
          v-model="ruleForm2.cardId"
          autocomplete="off"
          placeholder="请输入身份证号"
        ></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="pwd" class="pass">
        <el-input
          type="password"
          v-model="ruleForm2.pwd"
          autocomplete="off"
          placeholder="请输入密码"
        ></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="confirmPwd">
        <el-input
          type="password"
          v-model="ruleForm2.confirmPwd"
          autocomplete="off"
          placeholder="请再次输入密码"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm2')"
          >提交</el-button
        >
        <el-button @click="resetForm('ruleForm2')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm2.pwd) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      ispass: true,
      ruleForm2: {
        cardId: "",
        pwd: "",
        confirmPwd: ""
      },
      rules2: {
        pwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { min: 6, message: "密码不能少于6位字符", trigger: "blur" }
        ],
        cardId: [
          { required: true, message: "身份证号不能为空", trigger: "blur" }
        ],
        confirmPwd: [{ validator: validatePass2, trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let studentId = this.$cookies.get("cid");
          this.$axios({
            //修改密码
            url: `/api/sreset`,
            method: "post",
            data: {
              pwd: this.ruleForm2.pwd,
              studentId,
              cardId: this.ruleForm2.cardId
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                showClose: true,
                message: "修改密码成功，请重新登录",
                type: "success"
              });
              this.$router.push({
                name: "login",
                params: {
                  username: studentId
                }
              });
            } else if (res.data.code == 111) {
              this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            } else {
              this.$message({
                showClose: true,
                message: "身份信息与当前账号不匹配，请重试",
                type: "error"
              });
            }
          });
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style scoped>
#manager .pass label {
  color: red;
  font-size: 20px;
}
#manager {
  width: 600px;
  margin: 0 auto;
  margin-top: 100px;
  text-align: center;
  margin-bottom: 300px;
}
#manager .alter {
  margin: 30px 0px;
}
</style>



